import axios from 'axios'
import iView from 'iview'

/**
 *  axios  RESTful HTTP 
 * @author  <729234283@qq.com>
 */
export default class REST {
  /**
   * 
   */
  constructor () {
    /**
     * 
     * @type {string}
     */
    this.baseURL = ''

    /**
     * 
     * @type {string}
     */
    this.version = ''

    /**
     * 
     * @type {string}
     */
    this.path = ''

    /**
     * Headers
     * @type {Object}
     */
    this.headers = {"Content-Type":"application/json"}

    /**
     * 
     * @type {Function}
     */
    this.errorHandler = () => {}

    // 
    const methods = ['GET', 'POST', 'PATCH', 'PUT', 'DELETE']

    //  this
    methods.forEach((method) => {
      this[method] = options => this._request(method, options)
    })
  }

  /**
   * 
   * @param {string} method='GET' 
   * @param {Object} [options={}] 
   * @param {string} [options.uri='']  ID
   * @param {Object} [options.params=null] GET 
   * @param {Object} [options.data=null] POST/PUT/PATCH 
   * @return {Object}
   */
  _request (method = 'GET', options = {}) {
    const {uri = '', params = null, data = null} = options
    let url = this.version ? `/${this.version}/${this.path}` : `/${this.path}`

    if (uri) {
      url = `${url}/${uri}`
    }

    // GET
    if (params) {
      url = url + this._objToUrl(params)
    }

    return new Promise((resolve, reject) => {
      axios({
        baseURL: this.baseURL,
        headers: this.headers,
        method,
        url,
        data
      }).then((res) => {
        if(res.data.success){
          res && resolve(res)  
        }else{
          if (res.data.data) {
            iView.Message.error(res.data.data.message || res.data.errMsg);  
          }else{
            iView.Message.error(res.data.errMsg);
          }
        }
        
      }).catch(this.errorHandler)
    })
  }

  /**
   *  URL
   * @param {Object} obj 
   * @return {string}
   */
  _objToUrl (obj) {
    if (!obj || !Object.keys(obj).length) {
      return ''
    }

    return '?' + Object.keys(obj).map((key) => {
      return `${key}=${encodeURIComponent(obj[key])}`
    }).join('&')
  }

  /**
   * 
   * @param {string} [path=''] 
   */
  addPath (path = '') {
    this.path = this.path + '/' + path

    return this
  }

  /**
   *  Headers
   * @param {Object} headers Headers
   */
  addHeaders (headers) {
    this.headers = {
      ...this.headers,
      ...headers
    }

    return this
  }

  /**
   * 
   * @param {Object} options={} 
   */
  replace (options = {}) {
    Object.keys(options).forEach((key) => {
      this.path = this.path.replace(new RegExp('{' + key + '}', 'img'), options[key])
    })

    return this
  }
}



