          <Button type="ghost" icon="android-person">admin</Button>
          <Button type="ghost" icon="log-out" @click="handleLogout"></Button>
        </Button-group>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'header',
    methods: {
      handleLogout () {
        this.$router.push('/logout')
        this.$Message.success('')
