import iView from 'iview'
import auth from './auth'

/**
 * REST 
 * @type {Object}
 */
export default {
  /**
   * 
   * @param {Object} res 
   */
  successHandler (res) {},

  /**
   * 
   * @param {Object} res 
   */
  errorHandler (res) {
    iView.Message.error(res.response.data.error)
    if (res.response.data.error == 'Unauthorized') {
      auth.logout ()  
    }
  },

  /**
   *  Headers
   * @return {Object}
   */
  getHeaders () {
    const {manager, token} = auth.get()

    return {
      auth: window.btoa(`${manager.username}\n${token}`)
    }
  }
}



